import { getFirestore, collection, doc, getDoc, addDoc, updateDoc, query, onSnapshot } from 'firebase/firestore'
import { reactive } from 'vue'

const goodsList = reactive({})
const db = getFirestore()

onSnapshot(query(collection(db, 'WAREHOUSE')), snapshot => {
	snapshot.docChanges().forEach(change => {
		if (change.type === 'added' || change.type === 'modified') {
			goodsList[change.doc.id] = change.doc.data()
		} else if (change.type === 'removed') {
			delete goodsList[change.doc.id]
		}
	})
})

const addGoods = async newData => {
	const { goodsName, group, retailPrice, wholesalePrice } = newData
	const docRef = await addDoc(collection(db, 'WAREHOUSE'), {
		goodsName,
		group,
		retailPrice,
		wholesalePrice,
		stockAvail: {},
		importNoteIDList: [],
		exportNoteIDList: [],
		createdAt: new Date().getTime(),
		updatedAt: new Date().getTime(),
		removedAt: 0,
	})

	const docSnap = await getDoc(docRef)
	return {
		goodsID: docSnap.id,
		...docSnap.data(),
	}
}

const updateGoods = async (id, { retailPrice, wholesalePrice }) => {
	const docRef = doc(db, 'WAREHOUSE', id)
	await updateDoc(docRef, {
		retailPrice,
		wholesalePrice,
		updatedAt: new Date().getTime(),
	})

	const docSnap = await getDoc(docRef)
	return {
		goodsID: docSnap.id,
		...docSnap.data(),
	}
}

export { goodsList, addGoods, updateGoods }
