<template>
	<a-form :label-col="{ flex: '120px' }" :wrapper-col="{ flex: 'auto' }" class="form">
		<a-form-item label="Customer">
			<InputSearchCustomer
				ref="inputSearchCustomer"
				@searchingCustomer="handleSearchingCustomer"
				@selectCustomer="handleSelectCustomer"
				style="width: calc(100% - 170px)"
			/>
			<a-button type="primary" @click="openModalCreateCustomer">Create New Customer</a-button>
			<ModalCreateCustomer ref="modalAddCustomer" @newCustomerCreated="handleCreateNewCustomer" />
		</a-form-item>
		<a-form-item label="Goods List">
			<div style="display:flex; justify-content: flex-end">
				<a-button @click="openModalAddStockOut">
					<template #icon>
						<PlusOutlined />
					</template>
					Add Goods
				</a-button>
			</div>
			<ModalAddStockOut ref="modalAddStockOut" @addStockOut="addStockOut" />
		</a-form-item>
		<div class="wrapper-table">
			<table class="table">
				<thead>
					<th>#</th>
					<th>Name</th>
					<th>Expiry Date</th>
					<th>Quantity</th>
					<th>Actual/Expected</th>
					<th>Total</th>
					<th>Action</th>
				</thead>
				<tbody>
					<tr v-if="Object.keys(stockOut).length === 0">
						<td colspan="7" style="text-align:center">No data available in table</td>
					</tr>
					<template v-for="(goods, goodsID, goodsIndex) in stockOut" :key="goodsIndex">
						<tr
							v-for="({ quantity, expectedPrice, actualPrice }, expiryDate, stockIndex) in goods"
							:key="stockIndex"
						>
							<td style="text-align: center" v-if="stockIndex === 0" :rowspan="Object.keys(goods).length">
								{{ goodsIndex + 1 }}
							</td>
							<td v-if="stockIndex === 0" :rowspan="Object.keys(goods).length">
								{{ goodsList[goodsID].goodsName }}
							</td>
							<td style="text-align: right">{{ formatDateTime(Number(expiryDate)) }}</td>
							<td style="text-align: right">{{ quantity }}</td>
							<td style="text-align: right">{{ actualPrice }} / {{ expectedPrice }}</td>
							<td style="text-align: right">{{ quantity * actualPrice }}</td>
							<td style="text-align: center">
								<EditTwoTone
									twoToneColor="orange"
									class="delete-row-goods"
									@click="editStockOut(goodsID, expiryDate)"
								/>
							</td>
						</tr>
					</template>
					<tr style="text-align: right">
						<td colspan="5">Ship ({{ shipping.whoPays }} pays)</td>
						<td>{{ shipping.cost }}</td>
						<td></td>
					</tr>
					<tr style="text-align: right; font-weight: bold">
						<td colspan="5">Total Money</td>
						<td>{{ totalMoney }}</td>
						<td></td>
					</tr>
					<tr style="text-align: right">
						<td colspan="5">Already Paid</td>
						<td>{{ payment.alreadyPaid }}</td>
						<td></td>
					</tr>
					<tr style="text-align: right">
						<td colspan="5">Debt</td>
						<td>{{ totalMoney - payment.alreadyPaid }}</td>
						<td></td>
					</tr>
				</tbody>
			</table>
		</div>
		<a-form-item label="Shipping Cost">
			<a-input v-model:value.number="shipping.cost" addon-after=".000 vnđ" type="number"></a-input>
		</a-form-item>
		<a-form-item label="Who pays">
			<a-radio-group v-model:value="shipping.whoPays">
				<a-radio value="Seller">Seller</a-radio>
				<a-radio value="Buyer">Buyer</a-radio>
			</a-radio-group>
		</a-form-item>
		<a-form-item label="Shipping Unit">
			<a-radio-group v-model:value="shipping.unit">
				<a-radio v-for="(unit, index) in UTILS_SHIPPING.unit" :key="index" :value="unit">{{ unit }}</a-radio>
			</a-radio-group>
		</a-form-item>
		<a-form-item label="Already Paid">
			<a-input v-model:value.number="payment.alreadyPaid" addon-after=".000 vnđ" type="number"></a-input>
		</a-form-item>
		<a-form-item label="Payment Method">
			<a-radio-group v-model:value="payment.method">
				<a-radio v-for="(method, index) in UTILS_PAYMENT.method" :key="index" :value="method">
					{{ method }}
				</a-radio>
			</a-radio-group>
		</a-form-item>
		<a-form-item>
			<div style="display: flex; justify-content: center; margin-top: 1rem">
				<a-button type="primary" :loading="loadingCreateNote" @click="startCreateNote">
					Create
				</a-button>
				<a-button style="margin-left: 10px" @click="handleCancleForm">
					Cancel
				</a-button>
			</div>
		</a-form-item>
	</a-form>
</template>

<script>
import { ref, reactive } from 'vue'
import { PlusOutlined, EditTwoTone } from '@ant-design/icons-vue'
import { addExportNote } from '@/firebase/useExportNote'
import { goodsList } from '@/firebase/useWarehouse'
import InputSearchCustomer from '@/components/common/InputSearchCustomer.vue'
import ModalCreateCustomer from '@/components/common/ModalCreateCustomer.vue'
import ModalAddStockOut from '@/components/export-note/export-note-add/ModalAddStockOut.vue'
import { MyFormatDateTime } from '@/utils/convert'
import { UTILS_SHIPPING, UTILS_PAYMENT } from '@/utils/constants'

export default {
	components: {
		InputSearchCustomer,
		ModalCreateCustomer,
		ModalAddStockOut,
		PlusOutlined,
		EditTwoTone,
	},
	setup() {
		const stockOut = reactive({})

		return {
			goodsList,
			customerID: ref(''),
			shipping: reactive({
				cost: '',
				unit: 'Viettel Post',
				whoPays: '-',
			}),
			payment: reactive({
				alreadyPaid: 0,
				method: 'Bank Transfer',
			}),
			stockOut,
			loadingCreateNote: ref(false),
			UTILS_SHIPPING,
			UTILS_PAYMENT,
		}
	},
	computed: {
		totalMoney() {
			const totalPrice = Object.values(this.stockOut).reduce((accGoods, goods) => {
				const eachGoods = Object.values(goods).reduce((accDate, date) => {
					const eachDate = date.quantity * date.actualPrice
					return accDate + eachDate
				}, 0)
				return accGoods + eachGoods
			}, 0)
			if (this.shipping.whoPays === 'Seller') return totalPrice
			return totalPrice + this.shipping.cost
		},
	},
	methods: {
		handleSearchingCustomer() {
			this.customerID = ''
		},
		handleSelectCustomer(e) {
			this.customerID = e.id
		},
		openModalCreateCustomer() {
			this.$refs.modalAddCustomer.openModal()
		},
		handleCreateNewCustomer(e) {
			this.customerID = e.customerID
			this.$refs.inputSearchCustomer.customerID = e.customerID
			this.$refs.inputSearchCustomer.customerName = e.customerName
		},
		openModalAddStockOut() {
			this.$refs.modalAddStockOut.openModal()
		},
		addStockOut({ goodsID, expiryDate, quantity, costPrice, expectedPrice, actualPrice }) {
			if (!this.stockOut[goodsID]) this.stockOut[goodsID] = {}
			if (!this.stockOut[goodsID][expiryDate]) this.stockOut[goodsID][expiryDate] = {}
			this.stockOut[goodsID][expiryDate] = {
				quantity: (this.stockOut[goodsID][expiryDate].quantity || 0) + quantity,
				costPrice,
				expectedPrice,
				actualPrice,
			}
		},
		editStockOut(goodsID, expiryDate) {
			const { expectedPrice, actualPrice, quantity } = this.stockOut[goodsID][expiryDate]
			this.$refs.modalAddStockOut.openModal({
				infoGoods: { goodsID, expiryDate },
				infoSell: {
					expectedPrice,
					quantity,
					discount: {
						number: expectedPrice - actualPrice,
						type: 'vnd',
					},
				},
			})
			delete this.stockOut[goodsID][expiryDate]
		},
		notify(message) {
			this.$notification.open({
				message: 'Error !!!',
				description: message,
				placement: 'topRight',
				duration: 2,
			})
		},
		checkValidation() {
			if (!this.customerID) {
				this.notify('Please enter special customer !')
				return false
			}
			if (Object.keys(this.stockOut).length === 0) {
				this.notify('You must choose at least one goods !')
				return false
			}
			return true
		},
		handleCancleForm() {
			this.$router.back()
		},
		async startCreateNote() {
			if (!this.checkValidation()) return
			this.loadingCreateNote = true
			const newNote = {
				customerID: this.customerID,
				shipping: this.shipping,
				payment: this.payment,
				stockOut: this.stockOut,
				totalMoney: this.totalMoney,
			}
			try {
				await addExportNote(newNote)
			} catch (error) {
				this.notify(error.toString())
			} finally {
				this.loadingCreateNote = false
				this.$router.back()
			}
		},
		formatDateTime(time) {
			return MyFormatDateTime(time, 'DD/MM/YY')
		},
	},
}
</script>

<style lang="scss" scoped>
.form {
	max-width: 800px;
	margin: 0 auto;
	.btn-add-goods {
		display: flex;
		justify-content: flex-end;
	}
}
.wrapper-table {
	width: 100%;
	overflow-x: scroll;
	margin-bottom: 0.5rem;
}
</style>
