<template>
	<div v-click-outside="handleClickOutside">
		<input :value="searchText" @input="handleChangeInput" @keydown="handleKeydown" class="input-search" />
		<div class="select-search" v-if="isSearching">
			<div
				v-for="(item, key, index) in filterData"
				:key="index"
				:class="{ active: index == indexFocus, 'item-search': true }"
				@click.prevent="handleSelectItem(index)"
			>
				<slot name="item" :index="index" :key="key" :item="item">
					<div class="item-json">{{ index }} - {{ key }} - {{ JSON.stringify(item) }}</div>
				</slot>
			</div>
		</div>
	</div>
</template>

<script>
import { ref } from 'vue'
import { MySearch } from '@/utils/convert'

export default {
	props: {
		searchText: String,
		dataSource: {
			type: [Array, Object],
		},
	},
	setup() {
		return {
			indexFocus: ref(-1),
			isSearching: ref(false),
		}
	},
	computed: {
		filterData() {
			return Object.assign(
				{},
				...Object.entries(this.dataSource)
					.filter(([key, value]) => this.logicFilter(value, this.searchText))
					.map(([key, value]) => ({ [key]: value })),
			)
		},
		dataLeng() {
			return Object.keys(this.filterData).length
		},
	},
	methods: {
		handleChangeInput(e) {
			this.$emit('update:searchText', e.target.value)
			this.$emit('searching', e.target.value)
			this.indexFocus = -1
			if (e.target.value) this.isSearching = true
			else this.isSearching = false
		},
		logicFilter(item, key) {
			return MySearch(item.goodsName, this.searchText)
		},
		handleKeydown(e) {
			if (e.keyCode === 40) {
				this.indexFocus += 1
				if (this.indexFocus >= this.dataLeng) this.indexFocus = 0
			}
			if (e.keyCode === 38) {
				this.indexFocus -= 1
				if (this.indexFocus < 0) this.indexFocus = this.dataLeng - 1
			}
			if (e.keyCode === 13) {
				this.handleSelectItem(this.indexFocus)
			}
		},
		handleSelectItem(index) {
			this.indexFocus = -1
			const [key, value] = Object.entries(this.filterData)[index]
			this.$emit('selectItem', { key, value })
			this.closeSelectItem()
		},
		handleClickOutside() {
			this.indexFocus = -1
			this.closeSelectItem()
		},
		closeSelectItem() {
			this.$nextTick(() => {
				this.isSearching = false
			})
		},
	},
}
</script>

<style lang="scss" scoped>
.input-search {
	position: relative;
	padding: 0.3rem 0.5rem;
	width: 100%;
	outline: none;
	border: 1px solid #d9d9d9;
	border-radius: 2px;
	&:hover {
		border-color: #40a9ff;
	}
	&:focus {
		border-color: #40a9ff;
		outline: none;
		box-shadow: 0 0 0 2px rgb(24 144 255 / 20%);
	}
}
.select-search {
	position: absolute;
	z-index: 9;
	background-color: #fff;
	top: 100%;
	left: 0;
	right: 0;
	max-height: 500px;
	border: 1px solid #d4d4d4;
	box-shadow: 5px 5px 4px #aaaaaa;
	overflow-y: scroll;
	.item-search {
		padding: 0.5rem;
		cursor: pointer;
		border-bottom: 1px solid #d4d4d4;
		&.active {
			background-color: DodgerBlue;
			color: #fff;
		}
		&:hover {
			background-color: #e9e9e9;
		}
		.item-json {
			text-overflow: ellipsis;
			overflow: hidden;
			white-space: nowrap;
		}
	}
}
</style>
