<template>
	<a-modal
		v-model:visible="visibleModal"
		style="top: 50px"
		width="600px"
		title="Add Goods"
		:afterClose="refreshModal"
		@ok="handleAddGoodsOk"
	>
		<a-form :label-col="{ flex: '100px' }" :wrapper-col="{ flex: 'auto' }">
			<a-form-item label="Goods">
				<InputSearchGoods
					v-model:goodsName="goods.goodsName"
					@selectGoods="handleSelectGoods"
					@searching="handleSearching"
				/>
			</a-form-item>
			<a-form-item label="Information">
				<div class="infomation-goods">
					<div class="infomation-title">Expiry Date</div>
					:
					{{ formatDateTime(goods.expiryDate) }}
				</div>
				<div class="infomation-goods">
					<div class="infomation-title">Group</div>
					:
					{{ goods.group }}
				</div>
				<div class="infomation-goods">
					<div class="infomation-title">Available</div>
					:
					{{ goods.quantity }}
				</div>
				<div class="infomation-goods">
					<div class="infomation-title">Cost Price</div>
					:
					{{ formatNumber(goods.costPrice, 3) }}
				</div>
			</a-form-item>
			<a-form-item label="Expected Sell">
				<a-radio-group v-model:value="expectedPrice">
					<a-radio :value="goods.retailPrice">Retail Price: {{ formatNumber(goods.retailPrice, 3) }}</a-radio>
					<a-radio :value="goods.wholesalePrice">
						Wholesale Price: {{ formatNumber(goods.wholesalePrice, 3) }}
					</a-radio>
				</a-radio-group>
			</a-form-item>
			<a-form-item label="Discount">
				<a-input-group style="display:flex">
					<a-input v-model:value="discount.number" />
					<a-select v-model:value="discount.type">
						<a-select-option value="vnd">.000</a-select-option>
						<a-select-option value="percent">%</a-select-option>
					</a-select>
				</a-input-group>
			</a-form-item>
			<a-form-item label="Actual Price">
				{{ formatNumber(actualPrice, 3) }}
			</a-form-item>
			<a-form-item label="Quantity">
				<a-input v-model:value.number="quantity" type="number" />
			</a-form-item>
			<a-form-item label="Total Price">
				{{ formatNumber(totalPrice, 3) }}
			</a-form-item>
		</a-form>
	</a-modal>
</template>

<script>
import { ref } from 'vue'
import InputSearchGoods from '@/components/common/InputSearchGoods.vue'
import { formatNumber, MyFormatDateTime } from '@/utils/convert'
import { goodsList } from '@/firebase/useWarehouse'

export default {
	components: { InputSearchGoods },
	setup() {
		return {
			goods: ref({
				goodsID: '',
				goodsName: '',
				group: '-',
				retailPrice: '',
				wholesalePrice: '',
				expiryDate: '',
				quantity: '-',
				costPrice: '',
			}),

			expectedPrice: ref('-'),
			quantity: ref(''),
			discount: ref({
				number: '',
				type: 'vnd',
			}),
			visibleModal: ref(false),
			confirmModalLoading: ref(false),
			formatNumber,
			goodsList,
		}
	},
	computed: {
		actualPrice() {
			if (this.discount.type === 'vnd') {
				return this.expectedPrice - this.discount.number
			}
			return this.expectedPrice * (1 - this.discount.number / 100)
		},
		totalPrice() {
			return this.actualPrice * this.quantity
		},
	},
	methods: {
		setDataModal({ infoGoods, infoSell }) {
			this.goods.goodsID = infoGoods.goodsID || ''
			this.goods.goodsName = infoGoods.goodsName || ''
			this.goods.group = infoGoods.group || ''
			this.goods.expiryDate = infoGoods.expiryDate || '--'
			this.goods.quantity = infoGoods.quantity || 0
			this.goods.costPrice = infoGoods.costPrice || 0
			this.goods.retailPrice = infoGoods.retailPrice || 0
			this.goods.wholesalePrice = infoGoods.wholesalePrice || 0

			this.expectedPrice = infoSell.expectedPrice || '-'
			this.quantity = infoSell.quantity || 0
			this.discount.number = infoSell.discount?.number || '0'
			this.discount.type = infoSell.discount?.type || 'vnd'
		},
		getGoodsByIdExpiry(goodsID, expiryDate) {
			return {
				goodsID,
				goodsName: this.goodsList[goodsID].goodsName,
				group: this.goodsList[goodsID].group,
				expiryDate: Number(expiryDate),
				quantity: this.goodsList[goodsID].stockAvail[expiryDate]?.quantity || '',
				costPrice: this.goodsList[goodsID].stockAvail[expiryDate]?.costPrice || '',
				retailPrice: this.goodsList[goodsID].retailPrice,
				wholesalePrice: this.goodsList[goodsID].wholesalePrice,
			}
		},
		openModal(data) {
			this.visibleModal = true
			if (!data) return
			this.setDataModal({
				infoGoods: this.getGoodsByIdExpiry(data.infoGoods.goodsID, data.infoGoods.expiryDate),
				infoSell: data.infoSell,
			})
		},
		refreshModal() {
			this.setDataModal({ infoGoods: {}, infoSell: {} })
		},
		handleSelectGoods({ goodsID, expiryDate }) {
			this.setDataModal({
				infoGoods: this.getGoodsByIdExpiry(goodsID, expiryDate),
				infoSell: {
					expectedPrice: this.goodsList[goodsID].retailPrice,
				},
			})
		},
		handleSearching($event) {
			this.setDataModal({ infoGoods: {}, infoSell: {} })
			this.goods.goodsName = $event
		},
		notify(message) {
			this.$notification.open({
				message: 'Error !!!',
				description: message,
				placement: 'topRight',
				duration: 2,
			})
		},
		checkValidation() {
			if (!this.goods.goodsID) {
				this.notify('Please enter special goods !')
				return false
			}
			if (!this.quantity || this.quantity > this.goods.quantity) {
				this.notify(`Number of quantity field must be between 1 and ${this.goods.quantity}`)
				return false
			}
			return true
		},
		handleAddGoodsOk() {
			if (!this.checkValidation()) return
			const newStock = {
				goodsID: this.goods.goodsID,
				expiryDate: this.goods.expiryDate,
				costPrice: this.goods.costPrice,
				quantity: this.quantity,
				expectedPrice: this.expectedPrice,
				actualPrice: this.actualPrice,
			}
			this.$emit('addStockOut', newStock)
			this.visibleModal = false
		},

		formatDateTime(time) {
			return MyFormatDateTime(time, 'DD/MM/YY')
		},
	},
}
</script>

<style lang="scss" scoped>
.infomation-goods {
	display: flex;
	.infomation-title {
		width: 120px;
	}
}
</style>
