<template>
	<InputAutoComplete
		v-model:searchText="searchText"
		:dataSource="convertGoodList"
		@searching="handleSearching"
		@selectItem="handleSelectItem"
	>
		<template v-slot:item="{ item: { goodsName, stockAvail } }">
			<p class="goods-name">{{ goodsName }}</p>
			<p class="goods-number">{{ formatDateTime(Number(stockAvail.expiryDate)) }} - {{ stockAvail.quantity }}</p>
		</template>
	</InputAutoComplete>
</template>

<script>
import { ref, computed } from 'vue'
import InputAutoComplete from '@/components/common/InputAutoComplete.vue'
import { goodsList } from '@/firebase/useWarehouse'
import { MyFormatDateTime } from '@/utils/convert'

export default {
	props: ['goodsName'],
	components: { InputAutoComplete },
	setup() {
		return {
			searchText: ref(''),
			goodsList,
		}
	},
	watch: {
		goodsName(newValue) {
			this.searchText = newValue
		},
	},
	computed: {
		convertGoodList() {
			const data = {}
			Object.entries(this.goodsList).forEach(([goodsID, goods]) => {
				const { stockAvail, ...goodsData } = goods
				if (Object.keys(stockAvail).length === 0) {
					data[`${goodsID}:-`] = {
						...goodsData,
						stockAvail: {
							expiryDate: '-',
							quantity: null,
							costPrice: null,
						},
					}
				} else {
					Object.entries(stockAvail).forEach(([expiryDate, stock]) => {
						data[`${goodsID}:${expiryDate}`] = {
							...goodsData,
							stockAvail: {
								expiryDate,
								...stock,
							},
						}
					})
				}
			})
			return data
		},
	},
	methods: {
		handleSearching(string) {
			this.$emit('update:goodsName', string)
			this.$emit('searching', string)
		},
		handleSelectItem({ key, value }) {
			this.searchText = value.goodsName
			this.$emit('update:goodsName', value.goodsName)
			this.$emit('selectGoods', {
				goodsID: key.split(':')[0],
				expiryDate: key.split(':')[1],
			})
		},
		formatDateTime(time) {
			return MyFormatDateTime(time, 'DD/MM/YY')
		},
	},
}
</script>

<style lang="scss" scoped>
.goods-name {
	font-weight: bold;
	margin: 0;
}
.goods-number {
	margin: 0;
}
</style>
