<template>
	<a-auto-complete
		v-model:value="customerName"
		:filter-option="handleFilterCustomerList"
		@search="handleSearching"
		@select="handleSelectCustomer"
	>
		<template #dataSource>
			<a-select-option
				v-for="(customer, customerID) in customerList"
				:key="customerID"
				:value="customer.customerName"
			>
				{{ customer.customerName }}
			</a-select-option>
		</template>
		<a-input placeholder="Search Customer">
			<template #suffix>
				<a-tooltip v-if="customerID" title="Configuration">
					<CheckCircleTwoTone twoToneColor="blue" />
				</a-tooltip>
				<a-tooltip v-else title="Customer are not exists">
					<CloseCircleTwoTone twoToneColor="red" />
				</a-tooltip>
			</template>
		</a-input>
	</a-auto-complete>
</template>

<script>
import { ref } from 'vue'
import { CloseCircleTwoTone, CheckCircleTwoTone } from '@ant-design/icons-vue'
import { customerList } from '@/firebase/useCustomer'
import { MySearch } from '@/utils/convert'

export default {
	components: { CloseCircleTwoTone, CheckCircleTwoTone },
	setup() {
		return {
			customerList,
			customerID: ref(''),
			customerName: ref(''),
		}
	},
	methods: {
		handleSearching(value) {
			this.customerID = ''
			this.$emit('searchingCustomer', value)
		},
		handleSelectCustomer(value, option) {
			this.customerID = option.key
			this.$emit('selectCustomer', { id: option.key, data: customerList[option.key] })
		},
		handleFilterCustomerList(input, option) {
			return MySearch(option.value, input)
		},
	},
}
</script>

