import { getFirestore, collection, getDoc, addDoc, query, onSnapshot } from 'firebase/firestore'
import { reactive } from 'vue'

const customerList = reactive({})
const db = getFirestore()
const qr = query(collection(db, 'CUSTOMER'))

onSnapshot(qr, snapshot => {
	snapshot.docChanges().forEach(change => {
		if (change.type === 'added' || change.type === 'modified') {
			customerList[change.doc.id] = change.doc.data()
		} else if (change.type === 'removed') {
			delete customerList[change.doc.id]
		}
	})
})

const addCustomer = async data => {
	const docRef = await addDoc(collection(db, 'CUSTOMER'), {
		customerName: data.customerName,
		phone: data.phone,
		address: data.address,
		debt: data.debt || 0,
		createdAt: new Date().getTime(),
		updatedAt: new Date().getTime(),
		removedAt: 0,
	})

	const docSnap = await getDoc(docRef)
	const customerID = docSnap.id
	const { customerName, phone, address, debt } = docSnap.data()
	return {
		customerID,
		customerName,
		phone,
		address,
		debt,
	}
}

export { customerList, addCustomer }
