import {
	getFirestore,
	collection,
	doc,
	getDoc,
	query,
	where,
	orderBy,
	limit,
	onSnapshot,
	runTransaction,
	arrayUnion,
} from 'firebase/firestore'
import { reactive } from 'vue'

const nowTime = new Date().getTime()
const exportNoteList = reactive({})
const db = getFirestore()
const qr = query(
	collection(db, 'EXPORTNOTE'),
	where('updatedAt', '>', nowTime - 7 * 24 * 60 * 60 * 1000),
	orderBy('updatedAt', 'asc'),
	limit(10),
)

onSnapshot(qr, snapshot => {
	snapshot.docChanges().forEach(change => {
		if (change.type === 'added' || change.type === 'modified') {
			exportNoteList[change.doc.id] = change.doc.data()
		}
		if (change.type === 'removed') {
			delete exportNoteList[change.doc.id]
		}
	})
})

const addExportNote = async noteData => {
	const exportNoteRef = doc(collection(db, 'EXPORTNOTE'))

	await runTransaction(db, async transaction => {
		const queryGoodsRefList = Object.keys(noteData.stockOut).map(goodsID =>
			transaction.get(doc(db, 'WAREHOUSE', goodsID)),
		)
		const goodsDocList = await Promise.all(queryGoodsRefList)
		goodsDocList.forEach(goodsDoc => {
			const { stockAvail, goodsName } = goodsDoc.data() || {}
			Object.entries(noteData.stockOut[goodsDoc.id]).forEach(([expiryDate, { quantity }]) => {
				if (!stockAvail[expiryDate]) {
					throw new Error(`${goodsName} with ${expiryDate} are not available in warehouse`)
				}
				if (stockAvail[expiryDate].quantity < quantity) {
					throw new Error(
						`${goodsName} available is ${stockAvail[expiryDate].quantity}. But, you have just export ${quantity}`,
					)
				}
				if (stockAvail[expiryDate].quantity === quantity) {
					delete stockAvail[expiryDate]
					return
				}
				stockAvail[expiryDate].quantity -= quantity
			})
			transaction.update(doc(db, 'WAREHOUSE', goodsDoc.id), {
				stockAvail,
				exportNoteIDList: arrayUnion(exportNoteRef.id),
			})
		})

		transaction.set(exportNoteRef, {
			customerID: noteData.customerID,
			shipping: noteData.shipping,
			payment: noteData.payment,
			stockOut: noteData.stockOut,
			totalMoney: noteData.totalMoney,
			createdAt: new Date().getTime(),
			updatedAt: new Date().getTime(),
			removedAt: 0,
		})

		transaction.update(doc(db, 'CUSTOMER', noteData.customerID), {
			exportNoteIDList: arrayUnion(exportNoteRef.id),
		})
	})
}

const getExportNoteByID = async noteID => {
	const noteRef = doc(db, 'EXPORTNOTE', noteID)
	const noteSnap = await getDoc(noteRef)
	if (!noteSnap.exists()) throw new Error('No such document !')
	const { customerID, stockOut, ...noteData } = noteSnap.data()

	const getCustomerSnap = getDoc(doc(db, 'CUSTOMER', customerID))
	const getGoodsSnapList = Object.keys(stockOut).map(goodsID => getDoc(doc(db, 'WAREHOUSE', goodsID)))

	await Promise.all([getCustomerSnap, ...getGoodsSnapList]).then(([customerSnap, ...goodsSnapList]) => {
		noteData.customer = {
			customerID: customerSnap.id,
			customerName: customerSnap.data().customerName,
		}
		goodsSnapList.forEach(goods => {
			stockOut[goods.id].goodsName = goods.data().goodsName
		})
	})

	return {
		noteID: noteRef.id,
		customer: noteData.customer,
		stockOut,
		shipping: noteData.shipping,
		payment: noteData.payment,
		totalMoney: noteData.totalMoney,

		createdAt: noteData.createdAt,
		updatedAt: noteData.updatedAt,
		removedAt: noteData.removedAt,
	}
}

export { exportNoteList, addExportNote, getExportNoteByID }
