<template>
    <a-modal
        v-model:visible="visibleModal"
        width="900px"
        title="Create New Customer"
        :confirm-loading="confirmModalLoading"
        :afterClose="refreshModal"
        @ok="handleCreateCustomerOk"
    >
        <a-form :model="newCustomer" :label-col="{ span: 4 }" :wrapper-col="{ span: 20 }">
            <a-form-item label="Name">
                <a-input v-model:value="newCustomer.customerName"></a-input>
            </a-form-item>
            <a-form-item label="Phone">
                <a-input v-model:value="newCustomer.phone"></a-input>
            </a-form-item>
            <a-form-item label="Address">
                <a-input v-model:value="newCustomer.address"></a-input>
            </a-form-item>
        </a-form>
    </a-modal>
</template>

<script>
import { ref, reactive } from 'vue'
import { addCustomer } from '@/firebase/useCustomer'

export default {
    setup() {
        return {
            visibleModal: ref(false),
            confirmModalLoading: ref(false),
            newCustomer: reactive({ customerName: '', phone: '', address: '' }),
        }
    },
    methods: {
        openModal() {
            this.visibleModal = true
        },
        refreshModal() {
            this.newCustomer.customerName = ''
            this.newCustomer.phone = ''
            this.newCustomer.address = ''
            this.confirmModalLoading = false
        },
        async handleCreateCustomerOk() {
            this.confirmModalLoading = true
            const newCustomer = await addCustomer({
                customerName: this.newCustomer.customerName,
                phone: this.newCustomer.phone,
                address: this.newCustomer.address,
            })
            this.$emit('newCustomerCreated', newCustomer)
            this.visibleModal = false
        },
    },
}
</script>

